.page {
  text-align: left;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.page__body {
  margin: 0;
  padding: 0;
}

.header {
  box-sizing: border-box;
  background-position: center;
  background-size: cover;
  flex-direction: column;
  display: flex;
}

.header__bottom {
  grid-template-columns: 200px;
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .header__bottom {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .header__bottom {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .header__bottom {
    grid-template-columns: repeat(4, 200px);
  }
}

.top-bar {
  justify-content: space-between;
  align-items: center;
  padding-block: 0;
  padding-inline: 50px;
  display: flex;
}

.top-bar__header {
  justify-content: space-between;
  align-items: center;
  max-width: 100%;
  padding: 0 50px;
  display: flex;
}

.nav__list {
  flex-flow: row;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__list > :last-of-type:last-of-type {
  margin: 0;
}

.nav__item {
  margin-right: 20px;
}

.nav__link {
  cursor: pointer;
  color: #060b35;
  text-transform: uppercase;
  line-height: 60px;
  text-decoration: none;
  transition: color .3s;
  display: block;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__link--active {
  color: #00acdc;
  position: relative;
}

.nav__link--active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.logo {
  align-items: center;
  display: flex;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  transition: transform .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card-info__name {
  color: #34568b;
}

.card__photo {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__btn {
  color: #fff;
  text-transform: uppercase;
  box-sizing: border-box;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: background-color .3s;
  display: flex;
}

.card__btn:hover {
  color: #00acdc;
  background-color: #fff;
}

.card-info__name {
  text-align: left;
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card-info__code {
  text-align: left;
  color: #616070;
  margin-bottom: 16px;
  line-height: 14px;
}

.card-info__price {
  color: #616070;
  justify-content: space-between;
  margin-bottom: 16px;
  line-height: 18px;
  display: flex;
}

.card-info__price-value {
  text-align: right;
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.card-info__price-header {
  text-align: left;
  color: #616070;
  font-size: 12px;
}

.stars {
  flex-direction: row;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars.stars--1 :nth-child(-n+1), .stars.stars--2 :nth-child(-n+2), .stars.stars--3 :nth-child(-n+3), .stars.stars--4 :nth-child(-n+4), .stars.stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.stars :last-of-type {
  margin: 0;
}

.review {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.review__count {
  text-align: right;
  color: #060b35;
}
